<script setup lang="ts">
  import { ref, onMounted } from "vue"
  import { getQueryParams, setqueryParams } from "@/utils"
  import { MenuType } from "@/utils/enums"
  import { useRouter } from "vue-router"

  import PersonalInfo from "@/components/PersonalInfo.vue"

  interface MenuItem {
    title: string
    link: MenuType
    color?: string
  }

  const menus = ref<MenuItem[]>([
    {
      title: "All",
      link: MenuType.All,
      color: "error"
    },
    {
      title: "Photoshop",
      link: MenuType.PS,
      color: "info"
    },
    {
      title: "After Effects",
      link: MenuType.AE,
      color: "primary"
    },
    {
      title: "XD",
      link: MenuType.XD,
      color: "#ff60f8"
    },
    {
      title: "Icon",
      link: MenuType.ICON,
      color: "#470137"
    }
  ])
  const currentMenu = ref(menus.value[0].link)

  const router = useRouter()

  onMounted(() => {
    const urlParams = getQueryParams()

    if (urlParams.has("type")) {
      const urlType = urlParams.get("type") ? (urlParams.get("type") as MenuType) : MenuType.All
      if (urlType && Object.values(MenuType).includes(urlType)) {
        currentMenu.value = urlType
      }
    }
  })

  const changeType = (item: MenuItem) => {
    router.push({
      path: "/",
      query: {
        type: item.link
      }
    })
  }
</script>

<template>
  <v-layout class="container">
    <header class="d-flex justify-center align-center w-100 appTitleWrapper">
      <PersonalInfo />
      <div class="d-flex flex-column justify-center align-center w-100">
        <h1 class="mb-1 appTitle">Jessie Portfolio</h1>
        <h6 class="appSubTitle">Photography</h6>

        <v-tabs
          v-model="currentMenu"
          bg-color="transparent"
          color="basil"
          align-tabs="center"
          class="mt-6 w-100 menuWrapper"
          max-width="800px"
          center-active
        >
          <v-tab
            v-for="item in menus"
            :value="item.link"
            min-width="100"
            :color="currentMenu ? item.color || 'error' : 'transparent'"
            @click="changeType(item)"
          >
            {{ item.title }}
          </v-tab>
        </v-tabs>
      </div>
    </header>
  </v-layout>
</template>

