<script setup lang="ts">
  import { ref, reactive, onMounted, computed } from "vue"
  import { MenuType } from "@/utils/enums"
  import { useRoute } from "vue-router"

  import HeaderMenu from "@/components/HeaderMenu.vue"

  interface PortfolioItem {
    id: number
    type: MenuType
    name: string
    description: string
    imgUrl: string
    previewImgUrl?: string
  }

  const route = useRoute()

  const currentType = computed(() => {
    const { type } = route.query

    if (!type || !Object.values(MenuType).includes(type as MenuType)) {
      return MenuType.All
    }

    return type as MenuType
  })

  const isLoading = ref(false)
  onMounted(() => {
    isLoading.value = true
    Promise.all([getPortfolioList()]).finally(() => {
      isLoading.value = false
    })
  })

  const portfolioList = reactive<{
    list: PortfolioItem[]
  }>({
    list: []
  })
  const getPortfolioList = async () => {
    await fetch(
      "https://sheets.googleapis.com/v4/spreadsheets/1CmnsRmmiC06LQQ1JqGmL4za6XIfFYSMMY-gbWNMfie0/values/protfolioList?alt=json&key=AIzaSyA1dDhrQyh_WA_BtkCOYlinW1SYJPwTSsI"
    )
      .then((res) => res.json())
      .then((res: { values: [string, MenuType, string, string, string, string?][] }) => {
        const { values } = res

        if (!values.length) {
          portfolioList.list = []
          return
        }

        values.forEach((item) => {
          const [id, type, name, description, imgUrl, previewImgUrl] = item

          try {
            if (isNaN(parseInt(id)) || !name || !imgUrl) {
              return
            }
          } catch (e: any) {
            console.error(e)
            return
          }

          portfolioList.list.push({
            id: parseInt(id),
            type,
            name,
            description,
            imgUrl,
            previewImgUrl
          })
        })
      })
  }

  const filterPortfolioList = computed(() => {
    if (currentType.value === MenuType.All) {
      return portfolioList.list.filter((item) => !!item.name && !!item.imgUrl)
    }

    return portfolioList.list.filter((item) => item.type === currentType.value && !!item.name && !!item.imgUrl)
  })

  const visibleRef = ref(false)
  const indexRef = ref(0)
  const showImg = (index: number) => {
    indexRef.value = index
    visibleRef.value = true
  }
  const onHide = () => (visibleRef.value = false)
  const imgLightboxList = computed(() => {
    return filterPortfolioList.value.length
      ? filterPortfolioList.value.map((item) => ({
          title: item.name,
          src: item.imgUrl
        }))
      : []
  })
</script>

<template>
  <HeaderMenu />

  <div style="min-height: 300px">
    <v-container fluid style="max-width: 1024px">
      <v-row class="align-center justify-center py-2">
        <v-col
          cols="12"
          lg="3"
          md="3"
          sm="6"
          class="d-flex flex-column portFolioItem cursor-pointer"
          v-for="(item, index) in filterPortfolioList"
          :key="index"
          @click="showImg(index)"
        >
          <v-hover v-slot="{ isHovering, props }">
            <v-card v-bind="props" elevation="0">
              <v-img :src="item.previewImgUrl || item.imgUrl" :alt="item.name" aspect-ratio="1" cover></v-img>

              <v-overlay :model-value="isHovering" contained scrim="#fff" class="align-center justify-center">
                <v-card-title class="font-weight-bold">{{ item.name }}</v-card-title>
                <v-card-text class="font-weight-bold text-grey">{{ item.description }}</v-card-text>
              </v-overlay>
            </v-card>
          </v-hover>
        </v-col>
      </v-row>
    </v-container>
    <vue-easy-lightbox
      :visible="visibleRef"
      :imgs="imgLightboxList"
      :index="indexRef"
      @hide="onHide"
    ></vue-easy-lightbox>
  </div>
</template>

<style scoped lang="scss"></style>
