<script setup lang="ts">
  import { ref, reactive, onMounted, computed } from "vue"
  const menu = ref(false)

  const personalInfo = reactive({
    name: "",
    description: "",
    experienceList: [
      {
        name: "2017年 紅點 Best Of Best",
        link: "https://www.shoppingdesign.com.tw/post/view/2108",
        color: "error"
      },
      {
        name: "2017年 Adobe 卓越設計獎",
        link: "https://wjdesign.github.io/gallery2017/img/fun_4.jpg",
        color: "grey"
      },
      {
        name: "2017年 青春設計展 優選",
        link: "https://wjdesign.github.io/gallery2017/img/fun_4.jpg",
        color: "success"
      },
      {
        name: "2017年 放視大賞 銅獎",
        link: "http://www.ydf.org.tw/winners/8",
        color: "warning"
      }
    ]
  })

  const isLoading = ref(false)
  onMounted(() => {
    isLoading.value = true
    Promise.all([getPersonalInfo()]).finally(() => {
      isLoading.value = false
    })
  })

  const getPersonalInfo = async () => {
    await fetch(
      "https://sheets.googleapis.com/v4/spreadsheets/1CmnsRmmiC06LQQ1JqGmL4za6XIfFYSMMY-gbWNMfie0/values/personalInfo?alt=json&key=AIzaSyA1dDhrQyh_WA_BtkCOYlinW1SYJPwTSsI"
    )
      .then((res) => res.json())
      .then((res: { values: [string, string][] }) => {
        const { values } = res

        if (!values.length) {
          personalInfo.name = ""
          personalInfo.description = ""
          return
        }

        try {
          const [name, description] = values[1]
          personalInfo.name = name
          personalInfo.description = description
        } catch (e: any) {
          personalInfo.name = ""
          personalInfo.description = ""
        }
      })
  }

  const handleClick = (item: typeof personalInfo.experienceList[number]) => {
    window.open(item.link, "_blank")
  }
</script>

<template>
  <v-menu v-model="menu" :close-on-content-click="false" location="top">
    <template v-slot:activator="{ props }">
      <v-btn
        flat
        elevation="0"
        v-bind="props"
        class="btnPersonalInfo"
        icon="mdi-dots-vertical"
        size="large"
        :loading="isLoading"
      ></v-btn>
    </template>

    <v-card class="pa-2" width="90%" max-width="400">
      <v-card-title>{{ personalInfo.name }}</v-card-title>
      <v-card-text>
        <v-divider></v-divider>
      </v-card-text>
      <v-card-subtitle>Award experiences</v-card-subtitle>
      <v-card-text>
        <v-timeline truncate-line="both" density="compact" align="start">
          <v-timeline-item
            v-for="(item, index) in personalInfo.experienceList"
            :key="index"
            :dot-color="item.color"
            size="x-small"
            icon="mdi-book-variant"
            fill-dot
            class="cursor-pointer"
            @click="handleClick(item)"
          >
            {{ item.name }}
          </v-timeline-item>
        </v-timeline>
      </v-card-text>
      <v-card-text>
        <v-divider></v-divider>
      </v-card-text>
      <v-card-text>{{ personalInfo.description }}</v-card-text>
    </v-card>
  </v-menu>
</template>

<style scoped lang="scss">
  .btnPersonalInfo {
    position: fixed;
    bottom: 1.5rem;
    right: 1.5rem;
    z-index: 5;
  }
</style>
